import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

interface Location {
  city: string;
  state: string;
  country: string;
}

interface SocialNetworkProfile {
  network: string;
  url: string;
  fa: string;
}

interface Job {
  company: string;
  position: string;
  website: string;
  startDate: string;
  endDate: string;
  summary: string;
  highlights: string[];
}

interface Education {
  institution: string;
  area: string;
  studyType: string;
  startDate: string;
  endDate: string;
  gpa: string;
  areas_of_focus: string[];
}

interface Award {
  title: string;
  date: string;
  awarder: string;
  url: string;
  summary: string;
}

interface Publication {
  name: string;
  authors: string;
  publisher: string;
  releaseDate: string;
  url: string;
}

interface Project {
  name: string;
  type: string;
  date: string;
  description: string[];
  tech: Skill[];
  link: string[];
  paper_link: string;
}

interface Skill {
  name: string;
  fa: string;
}

interface Skills {
  name: string;
  keywords: Skill[];
}

interface Language {
  language: string;
  fluency: string;
}

interface Resume {
  name: string;
  label: string;
  pictureURL: string;
  email: string;
  phone: string;
  website: string;
  resume: string;
  summary: string;
  interests: string[];
  location: Location;
  profiles: SocialNetworkProfile[];
  work: Job[];
  education: Education[];
  awards: Award[];
  publications: Publication[];
  projects: Project[][];
  skills: Skills[];
  languages: Language[];
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent  implements OnInit{
  title = 'Mohammad Nadeem';
  public resumeData: Resume;
  constructor(private http: HttpClient) {
  }
  ngOnInit () {
    this.http.get<Resume>('../assets/resume.json').toPromise().then(data => {
      this.resumeData = data;
    });
  }
}
